.operation .title{
    margin-bottom: 20px;
    font-size: 14px;
    padding-bottom: 20px;
    border-bottom: 1px solid #666;
}
.operation-list li{
    display: flex;
    display: -webkit-flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16px;
}
.operation-list li .label{
    margin-right: 10px;
}
.wh{
    display: flex;
    align-items: center;
}
.wh input{
    border: 1px solid #999;
    border-radius: 4px;
}
.cutItem{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.cutItem div{
    flex-basis: 45%;
    height: 32px;
    line-height: 30px;
    font-size: 14px;
    cursor: pointer;
    text-align: center;
    border: 1px solid #666;
    border-radius: 2px;
    margin-bottom: 12px;
}